<template>	
<section class="wrap has_header_wrap c_wrap" ref="viewBox">
	<!-- 头部标题 -->
	<com-header titles="处方笺记录">
		<template  slot="style-def"></template>
	</com-header>
	<!-- 头部标题 end -->

	<!-- 记录列表 -->
	<div class="prescript_wrap prescript_detail" ref="viewB" id="viewB">
		<div class="item" v-for="(cd,index) in cfjData">
			<div class="pt_block solt d_flex">
				<span><i>{{index+1}}</i></span>
				<span>提交处方笺时间：</span>
				<span>{{ cd.createTime }}</span>
			</div>
			<div class="pt_block patient">
				<ul>
					<li class="d_flex">
						<span>姓名：</span><span>{{ cd.sufferer }}</span>
					</li>
					<li class="d_flex">
						<span>手机号码：</span><span>{{ cd.phone }}</span>
					</li>
					<li class="d_flex">
						<span>年龄：</span><span>{{ cd.age }}</span>
					</li>
					<li class="d_flex">
						<span>性别：</span><span>{{ cd.sex==1?'男':'女' }}</span>
					</li>
					<li class="d_flex">
						<span>医院：</span><span>{{ cd.hospitalName }}</span>
					</li>
					<li class="d_flex">
						<span>科别：</span><span>{{ cd.offices }}</span>
					</li>
					<li class="d_flex">
						<span>临床诊断：</span><span>{{ cd.diagnosed }}</span>
					</li>
				</ul>
			</div>
			<div class="pt_block pro">
				<mt-cell>
					<i class="icons_rx" slot="icon"></i>
				</mt-cell>
				<ul>
					<li class="d_flex" v-for="(cdp,index) in cd.prescriptionDetail">
						<div class="left">{{ index+1 }}</div>
						<div class="center">
							<p>{{ cdp.medicine }}</p>
							<span>{{ cdp.spec }}</span>
						</div>
					</li>
				</ul>
			</div>
			<div class="pt_block pt_upimg" v-show="cd.pictureURL">
				<h3>处方笺图片</h3>
				<div class="cfj_img">
					<img :src="cd.pictureURL">
				</div>
			</div>
		</div>
	</div>
	<!-- 记录列表 end -->
	
	<!-- 加载更多动画 -->
	<section class='loadMore' ref="loadMore" style="visibility:hidden;"><mt-spinner type="triple-bounce"></mt-spinner></section>
	<!-- 加载更多动画 end -->
</section>
</template>

<script>
import comHeaderTitle from '@/components/Common/ComHeaderTitle';
import { Cell, Button, MessageBox, Header, Field, Toast, Spinner } from 'mint-ui';

export default {
	name: 'info',
	data (){
		return {
			flag: false,
			img: './static/images/proimg.jpg',
			username: '', //患者姓名
			phone: '',//手机号码
			hospital: '',//医院
			ks: '', //科别
			zd: '', //诊断
			cfjData: [],//处方笺数据
			pagination: {
				page: 0,//页数
				count: '',//总条数
				limit: 10,//每页条数
			},
			noMore: false,//是否没有更多
		}
	},
	components: {
		'mt-cell'	: Cell,
		'mt-button'	: Button,
		'mt-header'	: Header,
		'mt-field'	: Field,
		'mt-spinner': Spinner,
		comHeaderTitle, // 公共头部标题
	},
	filters: {
		
	},
	created (){
		const _self = this;

		// this.$ajax({
		// 	url: _self.webRoot + '/prescription/getPrescription',
		// 	// url: '/prescription/getPrescription',
		// 	data: {
		// 		memberId: _self.$store.state.memberId,
		// 		page: _self.page,
		// 		limit: 10
		// 	}
		// })
		// .then(function(res){
		// 	// console.log(res)
		// 	if( res.data.success ){
		// 		if( res.data.data.length == 0 ){
		// 			_self.$options.methods.showToast('没有处方笺!');
		// 			return false;
		// 		}
		// 		_self.cfjData = res.data.data;
		// 	}else{
		// 		_self.$options.methods.showToast(res.data.msg);
		// 	}
		// })
		// .catch(function(err){
		// 	console.log(err)
		// })
		this.$options.methods.getList( this );
	},
	mounted (){
		const _self = this;
		this.$emit('changeClass',false);

		this.$nextTick(()=>{
			var box = this.$refs.viewBox;

	  		// 监听这个dom的scroll事件
			box.addEventListener('scroll', () => {
				if( this.noMore ){ return false; }

				var windowHeight = document.documentElement.clientHeight || document.body.clientHeight;
				// var boxb = this.$refs.viewB.getBoundingClientRect();
				var boxb = document.getElementById('viewB').offsetHeight;

				if( this.$refs.viewBox.scrollTop >= (boxb-windowHeight-100) ){
					//显示加载动画
					this.$refs.loadMore.style.visibility = 'visible';

					setTimeout(function(){
						//加载下一页
						_self.$options.methods.getList( _self );
					},300);
					console.log('到底部了');
				}
			}, false)
		});
	},
	destroyed (){
		this.$emit('changeClass',true);
	},
	methods: {
		jump (){
			console.log(111)
		},
		jump2 (el){
			console.log(el)
		},

		//公共函数
		//获取处方笺列表
		getList( _self ){
			if( _self.flag ){ return false; }

			_self.pagination.page++;
			if( _self.pagination.count ){
				if( _self.pagination.page > Math.ceil(_self.pagination.count/_self.pagination.limit) ){
					//超过最多页数时
					_self.noMore = true;//更新 - 没有更多
					_self.pagination.page--;
					_self.$options.methods.showToast('没有更多!');
					_self.$refs.loadMore.style.visibility = 'hidden';
					return false;
				}
			}

			_self.flag = true;
			_self.$ajax({
				url: _self.webRoot + '/prescription/getPrescription',
				// url: '/prescription/getPrescription',
				data: {
					memberId: _self.$store.state.memberId,
					page: _self.pagination.page,
					limit: _self.pagination.limit
				}
			})
			.then(function(res){
				console.log(res)
				if( res.data.success ){
					//更新总条数
					_self.pagination.count = res.data.count;

					if( res.data.data.length == 0 ){
						_self.$options.methods.showToast('没有处方笺!');
						return false;
					}
					// _self.cfjData = res.data.data;
					for( let i=0; i<res.data.data.length; i++ ){
						_self.cfjData.push(res.data.data[i]);
					};
					_self.flag = false;
				}else{
					_self.$options.methods.showToast(res.data.msg);
					_self.flag = false;
				}
			})
			.catch(function(err){
				console.log(err);
				_self.flag = false;
			});
		},
	    //显示提示信息
	    showToast( msg, time ){
	      var time = time ? time : 2000;

	      Toast({
	        message: msg,
	        position: 'center',
	        duration: time
	      });     
	    },
	},
}	
</script>

<style scoped>
@import '../../../static/css/headerTitle.css';
@import '../../../static/css/childre_wrap.css';
@import './Css/prescript.css';

.c_wrap {
	padding-bottom: 0;
}
</style>


